import { OnInit } from '@angular/core';
import { Journal, JournalInformation, ISSN } from '../../entities/journal.entity';
import { PageEvent } from '@angular/material';
import { MetadataService } from '../../core/metadata.service';
import { MatPaginator, MatTableDataSource } from '@angular/material';
import { merge, of as observableOf } from 'rxjs';
import { startWith, switchMap, map, catchError } from 'rxjs/operators';
import { FiltersService } from '../../filters/filters.service';
import { environment } from '../../../environments/environment';
'../../environments/environment';
import { CatalogService } from '../../catalog/catalog.service';
import { HarvesterFiltersComponent } from '../harvester-filters/harvester-filters.component';
var RepositoriesComponent = /** @class */ (function () {
    function RepositoriesComponent(service, metadata, filterService) {
        this.service = service;
        this.metadata = metadata;
        this.filterService = filterService;
        this.journalList = [];
        this.loading = true;
        this.dataSource = new MatTableDataSource();
        this.columnsToDisplay = ['title'];
        this.length = 0;
        this.pageSize = 5;
        this.pageSizeOptions = [5, 10, 20, 40, 60, 80, 100];
        this.irokoHost = environment.irokoHost + "/catalog";
        this.currentlayout = {
            name: 'Izquierda',
            layout: 'row',
            aling: 'center baseline',
            width: '22'
        };
    }
    RepositoriesComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.metadata.setTitleDescription("Catálogo de Revistas Científicas", "");
        this.paginator.firstPage();
        this.paginator.pageSize = this.pageSize;
        this.service.getJournalsCount().subscribe(function (response) {
            _this.length = response.data.count;
        });
        this.fetchJournalData();
        this.filterService.paramsChanged.subscribe(function (params) {
            _this.params = params;
            _this.fetchJournalData();
        });
    };
    // onPaginatorChanged(){
    //   this.filterService.changeFilter('count',this.paginator.pageSize, false);
    //   this.filterService.changeFilter('page',this.paginator.pageIndex);
    // }
    RepositoriesComponent.prototype.fetchJournalData = function () {
        var _this = this;
        this.loading = true;
        //this.dataSource.data = this.service.getJournalsPage(this.count, this.page);
        var arr = new Array();
        merge().pipe(startWith({}), switchMap(function () {
            _this.loading = true;
            return _this.service.getJournalsPage(_this.paginator.pageSize, _this.paginator.pageIndex, _this.params);
        }), map(function (response) {
            // Flip flag to show that loading has finished.
            _this.loading = false;
            // this.isRateLimitReached = false;
            // this.resultsLength = response.total_count;
            _this.length = response.data.sources.count;
            response.data.sources.data.forEach(function (item) {
                var j = new Journal(0, 0);
                j.id = item.id;
                j.tocoID = item.uuid;
                j.source_type = item.source_type;
                j.harvest_type = item.harvest_type;
                j.harvest_endpoint = item.harvest_endpoint;
                var info = new JournalInformation();
                info.url = item.data != null ? item.data.url : "";
                info.title = item.name;
                info.subtitle = item.subtitle;
                info.shortname = item.shortname;
                var issn = new ISSN();
                issn.e = item.data != null ? item.data.issn.e : "";
                issn.l = item.data != null ? item.data.issn.l : "";
                issn.p = item.data != null ? item.data.issn.p : "";
                info.issn = issn;
                info.rnps = item.data != null ? item.data.rnps : "";
                info.logo = item.data != null ? item.data.logo : "";
                info.purpose = item.purpose;
                info.description = item.data != null ? item.data.description : "";
                j.jinformation = info;
                arr.push(j);
            });
            return arr;
        }), catchError(function (error) {
            _this.loading = false;
            console.log("ERRORRR  " + error);
            // Catch if the GitHub API has reached its rate limit. Return empty data.
            // this.isRateLimitReached = true;
            return observableOf([]);
        })).subscribe(function (data) { return _this.dataSource.data = data; });
    };
    RepositoriesComponent.prototype.onScrollUp = function () {
        // console.log("scrolled up!!");
    };
    RepositoriesComponent.prototype.isEmpty = function () {
        if (this.journalList.length == 0) {
            this.loading = false;
            return true;
        }
        return false;
    };
    RepositoriesComponent.prototype.isLoading = function () {
        return this.loading;
    };
    RepositoriesComponent.prototype.currentJournals = function () {
        console.log(this.dataSource.data);
    };
    return RepositoriesComponent;
}());
export { RepositoriesComponent };
